* {
  box-sizing: border-box; }

html {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 16px;
  font-weight: 500; }

body {
  min-height: 100vh;
  height: 100%;
  background-color: #fff; }

body,
ul,
p,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ol {
  margin: 0;
  padding: 0; }

a {
  text-decoration: none; }

ul,
ol {
  list-style: none; }

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 700; }

button,
input,
