.page-conteiner {
  min-height: 100vh;
  max-width: 980px;
  margin: 0 auto;
  background: #f5ca79; }

.product-list {
  padding: 6px; }
  .product-list__item {
    padding: 6px; }

.product {
  width: 100%; }
  .product__content {
    position: relative;
    overflow: hidden; }
  .product__preview-control {
    position: absolute;
    bottom: -48px;
    width: 100%;
    border: none;
    font-size: 1rem;
    text-align: center;
    padding: 15px;
    background-color: rgba(255, 255, 255, 0.4);
    transition: all 0.3s; }
  .product__image {
    width: 100%;
    display: block; }
    .product__image_scaled {
      position: absolute;
      top: 0;
      left: 0;
      opacity: 0;
      transform: scale(1);
      transition: all 0.3s;
      visibility: hidden; }
  .product:hover .product__image_scaled {
    opacity: 1;
    transform: scale(1.2);
    visibility: visible; }
  .product:hover .product__preview-control {
    transform: translateY(-100%); }
  .product__footer {
    background-color: #ffffff;
    padding-top: 15px; }
  .product__title {
    font-size: 1.06rem; }
  .product__price {
    margin-top: 5px; }

.price {
  display: flex;
  justify-content: flex-start; }
  .price__title {
    margin-right: 10px; }
    .price__title_sale {
      display: none; }
  .price_sale .price__title_regular {
    text-decoration: line-through;
    color: #696969; }
  .price_sale .price__title_sale {
    display: inherit; }
